.container {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto; 
}

@media (min-width: 576px) {
  .container {
    padding-left: 35px;
    padding-right: 35px;  
  }
}  

@media (min-width: $tablet-width) {
  .container {
    padding-left: 39px;
    padding-right: 39px;       
  }
}  

@media (min-width: 992px) {
  .container {
    padding-left: 30px;
    padding-right: 30px; 
  }
}  


@media (min-width: $desktop-width) {
  .container {
    width: 1280px;
    padding-left: 25px;
    padding-right: 25px;
    margin: 0 auto;

  }
}  

