.select {
    position: relative;
}

.select__head {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-width: 100%;    
    padding-left: 30px;
    padding-right: 20px;
    cursor: pointer;
    z-index: 100;
    overflow: hidden;
    overflow-y: auto;
}

.select__head::after {
    width: 16px;
    height: 16px;
    background-image: url("../img/icon-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    right: 30px;
    bottom: 50%;
    transform: translateY(50%);
    content: '';
    display: block;
    transition: .2s ease-in;
}

.select__head.open::after {
    transform: translateY(50%) rotate(180deg);
    background-image: url("../img/icon-arrow-red.svg");
}

.select__list {
    display: none;
    position: absolute;
    top: 100%;
    left: -1px;
    right: 0;
    background: #fff;
    border: 1px solid #e1e2e3;
    margin-top: 5px;
    max-height: 205px;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 100;
    margin: 0;
    padding: 0;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 14px;
    color: #000000;
    scrollbar-color: dark;
    scrollbar-width: thin;
    overscroll-behavior: contain;
}

html .select__list {
    scrollbar-width: thin;
    scrollbar-color: #e53935 #ddd;
 }

.select__item {
    height: 30px;
}

.select__list .select__item {
    position: relative; 
    cursor: pointer;
    list-style-type: none;
}

.select__list .select__item:hover {
    color: #E53935;
}

.select__list::-webkit-scrollbar
{
    background-color: transparent;
    border-radius: 8px;
    width: 30px;
    overflow: hidden;
    margin-right: 20px;
    border-left: 4px #DDDDDD solid;
    border-radius: 0;
}
.select__list::-webkit-scrollbar-thumb
{
    background-color: transparent;
    border-left: 4px red solid;
    border-radius: 0;
}

.select__list {
    scrollbar-color: #E53935;; 
}

