.cars {
    display: none;
}

@media (min-width: 576px) {
    .cars {
        padding-top: 8px;
        display: block;
    }

    .cars__slider {
        margin-left: 25px;     
    }

    .cars__slide {
        padding-left: 10px;
        padding-right: 10px;
        box-sizing: border-box;
        width: 290px;
        height: 435px;     
        margin-bottom: 50px;        
    }

    .cars__title {
        padding: 0;
        margin: 0;
        margin-bottom: 40px;
        font-family: "Roboto-bold";
        font-size: 30px;
        color: #1B1C20;
        text-align: center;
    }

      
    .cars__slider .slick-prev {    
        top: -90px;
        left: auto;
        right: 100px;
        z-index: 100;
        width: 59px;
        height: 59px;
        border: 1px solid #EBEBEB;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../img/icon-prev-arrow.svg");
    
        &::before {
            content: "";
        }
    }
    
    .cars__slider .slick-next {
        top: -90px;
        left: auto;
        right: 21px;
        z-index: 100;
        width: 59px;
        height: 59px;
        border: 1px solid #EBEBEB;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../img/icon-next-arrow.svg");
    
        &::before {
            content: "";
        }
    }
    
    .cars__slider .slick-prev:hover,.cars__slider .slick-prev:focus {
        background-image: url("../img/arrow-active-prev.png");
    }   
    
    .cars__slider .slick-next:hover,.cars__slider .slick-next:focus {
        background-image: url("../img/arrow-active-next.png");
    }   
    
}    

@media (min-width: 768px) {    

    .cars__title {
        font-size: 36px;
        padding-right: 30px;
        margin-bottom: 50px;
    }

    .cars__slider {
        margin-left: 29px;
    }

    .cars__slide {
        padding-left: 10px;
        padding-right: 10px;
    }
}    

@media (min-width: 992px) {
    .cars {
        padding-top: 40px;
        padding-bottom: 0;
        padding-left: 30px;
        padding-right: 30px;
    }

    .cars__title {       
        margin-bottom: 60px;
        text-align: left;
        font-size: 40px;
    }

    .cars__slider {
        margin-left: -15px;
        margin-right: -15px;
    }

    .cars__slide {
        width: auto;
        height: 435px;
        padding-left: 15px;
        padding-right: 15px;
        //margin-bottom: 10px;
    }

}    

@media (min-width: $desktop-width) {
    .cars {
        margin: 0 auto;
        padding-top: 95px;
        padding-left: 25px;
        padding-right: 25px;
        width: 1280px;
    }

    .cars__slider {
        padding-left: 0;
        padding-right: 0;
    }

    .cars__slide {
        width: auto;
        height: 435px;
        padding-left: 15px;
        padding-right: 15px;
    }

    .cars__title {
        margin-bottom: 80px;
        font-size: 44px;
    }

    .cars__slider .slick-prev {    
        top: -107px;
    }
    
    .cars__slider .slick-next {    
        top: -107px;
    }
    
}    

@media (min-width: 1526px) {
    .cars__slider .slick-prev {    
        top: 45%;
        left: -115px;
        right: auto;
        width: 68px;
        height: 68px;
        transform: translateY(-50%);
    }
    
    .cars__slider .slick-next {    
        top: 45%;
        left: auto;
        right: -115px;
        width: 68px;
        height: 68px;
        transform: translateY(-50%);
    }
    
}    


