.button {
  position: relative;  
  text-align: center;
  background-color: #E53935;
  border: none;
  padding-top: 22px;
  padding-bottom: 22px;
  padding-left: 20px;
  padding-right: 20px;  
  font-family: "Roboto-bold";
  font-size: 17px;
  line-height: 17px;
  overflow: hidden;
  z-index: 1;
  color: #ffffff;
  transition: all 0.3s linear;

  &:hover {
    background-color: #000000;
  }

  &:active {
    background-color: #000000;
  }
} 

.main-screen__consult .container {  
  background: url(../img/main-bg-consult.jpg) no-repeat 102% -76%;
}