@media (max-width: $mobile-width-only) {

    body.nav--opened {
        overflow-y: hidden;
    }
    
    .nav {
        opacity: 0;
        width: 0;
        height: 0;
        transition: all 0.5s ease-out;
    }

    body.nav--opened .nav { 
        opacity: 1;
        display: flex;
        position: absolute;
        top: 120px;
        left: 0;
        height: calc(100vh - 71px);
        width: 100%;
        padding: 0;
        padding-top: 10px;
        padding-bottom: 120px;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: flex-start;
        z-index: 1000;
        background-color: #ffffff;
        transition: all 0.5s ease-out;
    }

    .nav__wrap {
        width: 100%;
    }

    .nav__list {
        padding: 0;
        margin: 0;
        margin-bottom: 40px;
        list-style: none;
        border: 1px solid #E1E2E3;
    }

    .nav__item {
        border-bottom: 1px solid #E1E2E3;
        
        &:last-child {
            border-bottom: none;
        }
    }
    

    .nav__link {
        display: block;
        padding-top: 19px;
        padding-bottom: 19px;
        padding-left: 20px;
        cursor: pointer;
        font-size: 16px;
        font-family: "Roboto-bold";
        color: #1B1C20;

        &:hover {
            color: #E53935;
        }

        &:active {
            color: #E53935;
        }
    }

    .nav__link--active {
        display: none;       
    }

    .nav__block-title {
        padding-bottom: 20px;
        padding-left: 20px;
        font-size: 16px;
        font-family: "Roboto-bold";        
        color: #e53935;
        border-bottom: 1px solid #E1E2E3;
    }

    .nav__accordeon-container {
        position: relative;
        padding-top: 17px;
    }

    .nav__btn {
        display: block;
        background-color: transparent;
        border: 0;
        outline: 0;
        position: absolute;
        width: 100%;
        height: 58px;
        top: 0;
        left: 0;
        border: none;
    }
    
    .nav__btn-icon {
        position: absolute;        
        right: 20px;
        top: 25px;
        background-repeat: no-repeat;
        background-size: contain;
        width: 10px;
        height: 10px;
        background-color: #ffffff;
        background-image: url("../img/icon-arrow-red.svg");
        border: none;
        transition: .2s ease-in;
    }

    .nav__accordeon-container--closed .nav__btn-icon {
        top: 18px;        
        transform: translateY(50%) rotate(180deg);
        transition: .2s ease-in;
    }

    .nav__submenu {
        opacity: 1;
        margin: 0;
        padding: 0;
        padding-top: 22px;
        padding-left: 20px;
        list-style: none;
        background-color: #ffffff;
        box-sizing: content-box;
        max-height: 415px;
        overflow-x: hidden;
        overflow-y: auto;   
        scrollbar-color: dark;
        scrollbar-width: thin;
        overscroll-behavior: contain; 
        transition: all 0.5s ease-out;
    }

    .nav__accordeon-container--closed .nav__submenu {
        padding: 0;
        margin: 0;
        margin-left: 40px;
        list-style: none;
        max-height: 0;     
        opacity: 0;       
        will-change: max-height;
        //display: none;           
        transition: all 0.5s ease-out;
    }    

    .nav__submenu-link {
        display: block;
        padding-top: 5px;
        padding-bottom: 5px;
        padding-left: 20px;
        cursor: pointer;
        font-size: 15px;
        color: #575757;

        &:hover {
            color: #E53935;
        }

        &:active {
            color: #E53935;
        }
    } 
    
    html .nav__submenu {
        scrollbar-width: thin;
        scrollbar-color: #e53935 #ddd;
     }
    
    .nav__submenu::-webkit-scrollbar
    {
        background-color: transparent;
        border-radius: 8px;
        width: 30px;
        overflow: hidden;
        margin-right: 20px;
        border-left: 4px #DDDDDD solid;
        border-radius: 0;
    }

    .nav__submenu::-webkit-scrollbar-thumb
    {
        background-color: transparent;
        border-left: 4px #E53935 solid;
        border-radius: 0;
    }

    .nav::-webkit-scrollbar
    {
        background-color: #DDDDDD;
        overflow: hidden;
    }

    .nav::-webkit-scrollbar-thumb
    {
        background-color: #E53935;
    }

    .nav__contacts-wrap {
        display: block;
    }

    .nav__address {
        position: relative;
        padding-top: 64px;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 20px;
        margin-bottom: 30px;
        font-size: 16px;
        color: #575757;
        border: 1px solid #E1E2E3;

        &::before {
            content: "";
            position: absolute;
            top: 18px;
            left: 20px;            
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-map.svg");
        }
    }

    .nav__contacts {
        position: relative;
        padding-top: 64px;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 20px;
        margin-bottom: 30px;        
        border: 1px solid #E1E2E3;

        &::before {
            content: "";
            position: absolute;
            top: 30px;
            left: 20px;            
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-clock.svg");
        }
    }

    .nav__tel {
        display: block;
        font-family: "Roboto-bold";
        font-size: 16px;
        color: #000000;

        &:hover {
            color: #E53935;
        }

        &:active {
            color: #E53935;
        }
    }

    .nav__email {
        position: relative;
        padding-top: 64px;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 20px;
        font-size: 16px;
        color: #575757;
        border: 1px solid #E1E2E3;

        &::before {
            content: "";
            position: absolute;
            top: 30px;
            left: 20px;            
            background-repeat: no-repeat;
            background-size: contain;
            width: 20px;
            height: 16px;
            background-image: url("../img/icon-email-red.svg");
        }
    }

    .nav__email-link {
        color: #E53935;
    }
}    

@media (min-width: 576px) {

    .nav {
        top: 133px;
    }

    .nav__contacts-wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .nav__address {
        grid-column: 1/3;
        margin-bottom: 0;
    }

    .nav__contacts {
        grid-column: 1/2;
        margin-bottom: 0;

        &::before {
            top: 18px;
        }
    }

    .nav__email {
        grid-column: 2/3;

        &::before {
            top: 18px;
            left: 18px;
        }
    }

}    

@media (min-width: 768px) {  
    .nav {
        top: 144px;
    }

    .nav__list {
        margin-bottom: 50px;
    }

    .nav__contacts-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 0;
    }

    .nav__address {
        width: 31%;
        
    }

    .nav__contacts {
        width: 31%;
    }

    .nav__email {
        width: 31%;
    }
}    

@media (min-width: 992px) {
    .nav {
        top: 150px;
    }

    .nav__accordeon-container {
        padding-top: 20px;
    }

    .nav__list {
        margin-bottom: 60px;
    }

    .nav__contacts {
        display: none;
    }

    .nav__address {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 23px;
            left: 30px;
        }
        
    }

    .nav__email {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 26px;
            left: 30px;
        }
    }
}    



@media (min-width: 1280px) {
    .nav {
        position: static;
        top: 0;
        height: auto;
        padding-top: 25px;
        padding-bottom: 25px;
        background-color: #F5F5F5;
    }

    .nav__block-title {
        font-family: "Roboto-bold";
        color: #E53935;
    }

    .nav__btn {
        display: none;
    }
    
    .nav__list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;  
    }    

    .nav__item {
        padding-left: 10px;
        
        &:first-child {
            padding-left: 0;
        }
    }
    
    .nav__link {
        font-family: "Roboto-bold";
        font-size: 16px;
        color: #1B1C20;

        &:hover {
            color: #E53935; 
        }

        &:active {
            color: #E53935; 
        }
    }

    .nav__link--active {
        display: block;
        position: relative;
        color: #E53935;        
    }       

    .nav__contacts-wrap {
        display: none;
    }

    .nav__item--section {
        position: relative;
        cursor: pointer;       
        padding-right: 20px;
    
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 1px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 10px;
            height: 6px;
            background-image: url("../img/icon-open.svg");
            transition: .2s ease-in;
        }

        &:hover {
            .nav__submenu {
                display: block;
                opacity: 1;      
                width: 1230px;           
                max-height: 495px;   
                transition: all 0.5s ease-out;         
            }      
            
            &::before {       
                top: 5px;
                transform: translateY(50%) rotate(180deg);
                transition: .2s ease-in;
            }
        }
    }

    .nav__accordeon-container {
        padding-top: 0;
    }

    .nav__submenu {
        position: relative;
        opacity: 0;
        margin: 0;
        padding-top: 70px;
        padding-bottom: 30px;
        padding-left: 40px;
        padding-right: 30px;
        list-style: none;
        column-count: 4;
        position: absolute;        
        top: 20px;
        left: 0;
        width: 0;
        overflow: hidden;
        max-height: 0; 
        background-color: #ffffff;
        z-index: 10;        
        transition: all 0.5s ease-out;
        box-shadow: 0px 20px 30px -12px rgba(34, 60, 80, 0.1);

        &::before {
            content: "";
            position: absolute;
            height: 27px;
            top: 0;
            left: 0;
            right: 0;
            background-color: #f5f5f5;
        }
    }

    .nav__submenu-item {
        margin-bottom: 8px;
    }

    .nav__submenu-link {
        color: #575757;
        font-size: 15px;
        cursor: pointer;

        &:hover {
            color: #E53935;
        }
    
        &:active {
            color: #E53935;
        }
    }
    
}    
