.form {
    display: flex;
    flex-direction: column;
}

.form__section {
    margin-bottom: 22px;
}

.form__text {
    width: 100%;
    padding-left: 30px;
    height: 60px;
    line-height: 58px;
    font-size: 15px;
    border: 1px solid #E1E2E3;
    box-sizing: border-box;

    &:hover {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:focus {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:active {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }

    &:disabled {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }
}

.form__text--files {
    display: none;    
}

#year-select {
    overflow-y:auto; 
    height: 60px;
}

.form__select {
    height: 60px;
    line-height: 58px;
    font-size: 15px;
    border: 1px solid #E1E2E3;
    box-sizing: border-box;
    width: 100%;
    appearance: none;
    background-repeat: no-repeat;
    background-position: right;
    background-position-x: calc(100% - 28px);
    //background-image: url("../img/icon-arrow.svg");  
   

    &:hover {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:focus {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:active {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }

    &:disabled {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }
}

option {   
    overflow-y:scroll;    
}

.form-section--files {
    order: 2;
}

.form__label--files {
    position: relative;
    display: block;
    width: 100%;
    padding-left: 50px;
    height: 62px;
    line-height: 60px;
    font-size: 15px;
    border: 1px solid #E1E2E3;
    box-sizing: border-box;

    &::before {
        content: "";
            position: absolute;
            top: 50%;
            left: 22px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-cross.svg");
    }

    &:hover {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:focus {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;

        &::before {
            background-image: url("../img/icon-cross-red.svg");
        }
    }

    &:active {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;

        &::before {
            background-image: url("../img/icon-cross-red.svg");
        }
    }

    &:disabled {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;

        &::before {
            background-image: url("../img/icon-cross-red.svg");
        }
    }
}

.form__send.button {
    margin-bottom: 20px;
    order: 4;
    font-size: 17px;
}

.form__info--files {
    position: relative;
    padding: 0;
    padding-left: 30px;
    margin: 0;
    margin-bottom: 18px;
    position: relative;
    font-size: 14px;
    color: #E53935;
    order: 1;

    &::before {
        content: "";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 20px;
            height: 24px;
            background-image: url("../img/icon-attention.svg");
    }
}

.form__info--text {
    padding: 0;
    margin: 0;
    margin-bottom: 18px;
    font-size: 14px;
    font-family: "TTNeue-regular";
    color: #000000;
    order: 3;

    span {
        color: #E53935;
    }
}

@media (min-width: 576px) {
    .form {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 31px;
    }

    .form__text {
        margin-bottom: 0;
    }

    .form__select {
        margin-bottom: 0;
    }

    .form__send {
        order: 1;
    }

    .form__section {
        margin-bottom: 0;
    }

    .form__send.button {
        margin-bottom: 0;
    }

    .form-section--files {
        order: 0;
    }

    .form__info--files {
        padding-top: 14px;

        &::before {
            width: 24px;
        }
    }

    .form__info--text {
        padding-top: 5px;
    }

    .form__send.button {
        order: 0;
    }  
    
}    

@media (min-width: 768px) {    
    .form__info--text {
        margin-bottom: 25px;
    }

    .form__info--files {
        padding-top: 2px;
    }
}    

@media (min-width: 992px) {  
    .form {
        gap: 32px;
    }

    .form__info--files {
        padding-top: 5px;
        font-size: 15px;
        
        &::before {
            top: 15px;
        }
    }

    .form__info--text {
        margin-top: -10px;
    }    

}    


@media (min-width: $desktop-width) {
    .form {        
        grid-template-columns: repeat(4, 1fr);
        gap: 40px;
        
    }    

    .form__send.button {
        font-size: 18px;
    }

    .form__text {
        height: 70px;
    }

    .form__select {
        height: 70px;
    }

    .form__label--files {
        line-height: 68px;
        height: 70px;
    }

    .form__send.button {
        font-size: 18px;
        padding-top: 23px;
        padding-bottom: 23px;
        margin-bottom: 0;
    }

    .form__info--files  {
        grid-column: 1/3;
        padding-top: 0;
        margin-top: 0;
    }

    .form__info--text  {
        margin-top: 0;
        grid-column: 3/5;
    }

}  

