.callback {
    position: relative;
}

.callback__form {
    position: absolute;
    bottom: -5px;
    left: 20px;
    right: 20px;
    transform: translateY(50%);
    z-index: 10000;
}

.callback__form-answer {  
    opacity: 0;  
    position: absolute;
    height: 0;
    font-size: 0;
    font-family: "Roboto-bold";
    bottom: -5px;
    left: 20px;
    right: 20px;
    background-color: #ffffff;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 30px;
    padding-right: 30px;
    z-index: 2;
    transform: translateY(50%);
    //transform: translate(150px, 50%);
    transition: all .5s;
    text-align: center;

    p {
        padding: 0;
        margin: 0;
    }

    span {
        color: #E53935;
    }
}

.callback--answer .callback__form-answer {
    opacity: 1;
    height: 390px;
    font-size: 24px;
    //transform: translate(0, 50%);
    transition: all .5s;  
    z-index: 200000;  
}

.callback__btn {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 24px;
    height: 24px;
    border: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../img/icon-close.svg");
    cursor: pointer;
    background-color: transparent;
}

.callback__map {
    width: 100%;
    height: 640px;
    background-image: url("../img/map-mobile.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #fafafa;
    overflow: hidden;  
    box-sizing: border-box;  
}

[class*="ymaps-2"][class*="-ground-pane"] {
    filter: grayscale(1);
    -ms-filter: grayscale(1);
    -webkit-filter: grayscale(1);
    -moz-filter: grayscale(1);
    -o-filter: grayscale(1);
}

.ymaps-2-1-79-gotoymaps__container,
.ymaps-2-1-79-gototech, .ymaps-2-1-79-gototaxi, .ymaps-2-1-79-copyright {
    display: none!important;
}

@media (min-width: 576px) {
    .callback__form {
        left: 35px;
        right: 35px;
        bottom: 0;
    }

    .callback__map {        
        background-image: url("../img/map-mobile-big.jpg");
    }

    .callback__form-answer {  
        padding-top: 92px;
        padding-bottom: 92px;
        left: 35px;
        right: 35px;
    }    

    .callback--answer .callback__form-answer {
        height: 336px;
    }    
}   

@media (min-width: 768px) {
    .callback__form {
        left: 39px;
        right: 39px;
    }      

    .callback__map {        
        background-image: url("../img/map-tablet.jpg");
    }

    .callback--answer .callback__form-answer {
        height: 350px;
    } 

    .callback__form-answer {  
        padding-top: 95px;
        padding-bottom: 95px;
        left: 39px;
        right: 39px;
    }  
}    

@media (min-width: 992px) {
    .callback__form {
        left: 30px;
        right: 30px;
    }      

    .callback__map {        
        background-image: url("../img/map-tablet-big.jpg");
    }

    .callback__form-answer {  
        padding-top: 50px;
        padding-bottom: 50px;
        left: 30px;
        right: 30px;
    }  

    .callback--answer .callback__form-answer {
        height: 240px;
    } 
}   

@media (min-width: $desktop-width) {
    .callback__form {
        bottom: 5px;
        left: calc((100vw - 1230px - 20px)/2);
        right: calc((100vw - 1230px - 20px)/2);
    }      

    .callback__map {        
        background-image: url("../img/map-desktop.jpg");
    }

    .callback__form-answer {  
        padding-top: 60px;
        padding-bottom: 80px;
        padding-left: 215px;
        padding-right: 215px;
        left: calc((100vw - 1230px - 20px)/2);
        right: calc((100vw - 1230px - 20px)/2);
    }  

    .callback--answer .callback__form-answer {
        height: 260px;
    } 
}  

