.advantages {
    padding-top: 20px;
}

.advantages__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 24px;
    text-align: center;
}

.advantages__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.advantages__item {
    position: relative;
    border: 1px solid #EBEBEB;
    padding-top: 103px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 19px;
    margin-bottom: 20px;
    font-size: 15px;

    &::before {
        content: "";
        position: absolute;
        top: 18px;
        left: 23px;
        width: 64px;
        height: 64px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.advantages__item--price {
    &::before {
        background-image: url("../img/icon-adv-price.svg");
    }    
}

.advantages__item--individually {
    &::before {
        background-image: url("../img/icon-adv-individually.svg");
    }    
}

.advantages__item--service {
    &::before {
        background-image: url("../img/icon-adv-service.svg");
    }    
}

.advantages__item--lawyers {
    &::before {
        background-image: url("../img/icon-adv-lawyers.svg");
    }    
}

.advantages__item--no-limits {
    &::before {
        background-image: url("../img/icon-adv-no-limits.svg");
    }    
}

.advantages__item-name {
    font-family: "Roboto-bold";
    padding: 0;
    margin-top: 0;
    margin-bottom: 15px;
    color: #1B1C20;
    font-size: 16px;
}

@media (min-width: 576px) {
    .advantages {
        padding-top: 39px;
    }

    .advantages__title {
        font-size: 30px;
        margin-bottom: 40px;
    }

    .advantages__item {
        padding-top: 30px;
        padding-left: 124px;
        padding-bottom: 27px;
        margin-bottom: 22px;
        font-size: 16px;

        &::before {
            top: 28px;
            left: 31px;
        }
    }

    .advantages__item-name {
        font-size: 18px;
        margin-bottom: 12px;
    }
}    

@media (min-width: 768px) {

    .advantages {
        padding-top: 40px;
    }

    .advantages__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .advantages__item {
        margin-bottom: 33px;
    }
}  

@media (min-width: 992px) {

    .advantages {
        padding-top: 60px;
    }

    .advantages__title {
        margin-bottom: 60px;
        font-size: 40px;
        text-align: left;
    }

    .advantages__list {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 30px;
    }

    .advantages__item {
        padding-top: 122px;
        padding-left: 29px;
        padding-right: 30px;
        margin-bottom: 0;
    }    

    .advantages__item-name {
        margin-bottom: 15px;
    }

    .advantages__item--price {
        grid-column: 1/3;
    }
    
    .advantages__item--individually {
        grid-column: 3/5;  
    }
    
    .advantages__item--service {
        grid-column: 5/7;  
    }

    .advantages__item--lawyers {
        grid-column: 1/4;
    }
    
    .advantages__item--no-limits {
          grid-column: 4/7;
    }
} 


@media (min-width: $desktop-width) {

    .advantages {
        padding-top: 130px;
    }

    .advantages__title {
        font-size: 44px;
        margin-bottom: 80px;
    }

    .advantages__item {
        padding-top: 30px;
        padding-left: 124px;
    }
} 

