.socials {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.socials__item {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: pink;
    margin-right: 20px;

    &:nth-child(3n) {
        margin-right: 0;
    }
}

.socials__link {
    text-decoration: none;
    color: inherit;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 150;
    }
}

.socials__link--telegram {
    background-color: #0088CC;

    &::before {
        width: 22px;
        height: 18px;
        background-image: url("../img/icon-telegram.svg");
    }

    &:hover {
        background-color: #007CBA;
    }

    &:active {
        background-color: #007CBA;
    }
}

.socials__link--whatsapp {
    background-color: #24CC63;

    &::before {
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-whatsapp.svg");
    }

    &:hover {
        background-color: #20AC55;
    }

    &:active {
        background-color: #20AC55;
    }
}

.socials__link--email {
    background-color: #E53935;

    &::before {
        width: 20px;
        height: 16px;
        background-image: url("../img/icon-email.svg");
    }

    &:hover {
        background-color: #000000;
    }

    &:active {
        background-color: #000000;
    }
}

.socials__link--tel {
    background-color: #E53935;

    &::before {
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-tel-white.svg");
    }

    &:hover {
        background-color: #000000;
    }

    &:active {
        background-color: #000000;
    }
}

@media (min-width: 992px) {    
    .socials__item {
        margin-right: 30px;
    }
}    

