    .slider-main {
        padding-top: 40px;

    }
    
    .slider-main__title {
        padding: 0;
        margin-top: 0;
        margin-bottom: 33px;
        font-family: "Roboto-bold";
        color: #1B1C20;
        font-size: 24px;
        text-align: center;
    }
    
    
    .slider-main__wrapper {
        overflow: hidden;
        border-left: 20px solid #ffffff;
    }
      
    .slider {
    display: block;    
    --slider-width: 100%; /*    */
    --slider-gap: 10px; /*    */
    --slider-gap-mobile-big: 20px;
    padding-bottom: 112px;
    
    margin-left: calc(20px / -2);
    /*margin-right: calc(20px / -2);*/
    }
    
      
    .group {
        display: grid !important;
        grid-template-rows: 1fr 1fr;
        grid-gap: var(--slider-gap);
    }
    
    .slide-content {
        position: relative;
        display: flex;
        /*padding: 0 calc(var(--slider-gap) / 2);*/
        width: 100%;
        height: 100%;
    }
    
    .video {  
        position: relative;
        padding: 0;
        margin: 0;
        margin-left: 10px;
        padding-right: 10px;        
        width: 100%;
        height: 100%;
        object-fit: cover;  
        background-color: transparent;       
    }
    
    .slide {       
        position: relative;
    }
    
    .slider .slick-prev {    
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    
    &::before {
        content: "";
    }
    }
    
    .slider .slick-next {
    position: absolute;
    top: auto;
    bottom: -8px;
    right: 25px;
    z-index: 100;
    width: calc(100vw - 40px);
    height: 59px;
    padding-right: 18px;
    border: 1px solid #E1E2E3;
    background-position: 73% center;
    background-repeat: no-repeat;
    background-image: url("../img/icon-arrow-small.svg");
    background-color: transparent;
    box-sizing: border-box;
    z-index: 1000;
    
    &::before {
        content: " ";
        font-family: "Roboto-regular";
        font-size: 15px;
        color: #1B1C20;
    }
    
    }  
    
    .slider .slick-next:hover,.slider .slick-next:focus {    
    background-image: url("../img/icon-arrow-active-small.svg");
    
    &::before {
        color: #E53935;
    }
    }   
    
    @media (max-width: 576px) {    
    
        .slide {
            height: 250px;
            /*     95%    */
            width: calc(var(--slider-width) * 0.95);
        }               
        
    }
    
    @media (max-width: 767px) and (min-width: 576px) {

        .slider-main {
            padding-top: 50px;
        }

        .slider-main__title {
            font-size: 30px;
            margin-bottom: 40px;
        }
            
        .slider-main__wrapper {
            border-left: 35px solid #ffffff;
        }

        .slider {
            padding-bottom: 115px;
        }
    
        .slide {
        height: 340px;
        width: calc(var(--slider-width) * 0.85);
        }
    
        .slide-content {        
            padding: 0 calc(20px / 2);        
        }
    
        .video {
            margin-left: 0;
            padding-right: 10px;
            padding-left: 10px;
        }        

        .slider .slick-next {
            bottom: -25px;
            width: 207px;
            left: calc((100vw - 207px)/2 - 17px);
            padding-right: 36px;
            background-position: 85% center;
        }  

        .slider .slick-next {
            bottom: -30px;
            width: 207px;
            left: calc((100vw - 207px)/2 - 17px);
            padding-right: 36px;
            background-position: 85% center;
        }   

        /*.group {        
            grid-gap: 20px;
        }*/
    }    
    
    
@media (min-width: 768px) {

    .slide {
        height: 155px;
    }

    .slider-main {
        padding-top: 55px;
    }
    
    .slider-main__title {
        font-size: 36px;        
        margin-bottom: 50px;
    }    

    .slider-main {
        padding-top: 60px;
        padding-left: 39px;
        padding-right: 39px;    
        max-width: 1280px;  
        border-left: none;
    }

    .slider-main__wrapper {
        border-left: 0;
    }

    .video {
        margin-left: 0;
        padding-right: 15px;
        padding-left: 15px;
    }

    
    .slider {
        /*--slider-gap: 30px;*/
        padding-bottom: 140px;
        margin: 0 -15px;     
    }
    
    .slick-track {
        display: flex;
    }
    
    .slick-slide {    
              
        //height: auto;
    
    }
    
    .first-slide {
        position: relative;
        /*      2/3    */
        width: calc(var(--slider-width) / 3 * 2);    
        height: 340px;
    }    
    
    .group {
        /*      1/3    */
        width: calc(var(--slider-width) / 3);
        grid-gap: 30px;    
    }

    .slider .slick-next {
        height: 68px;
        bottom: 20px;
        width: 207px;
        left: 50%;
        transform: translateX(-50%);
        padding-right: 36px;
        background-position: 85% center;        
    }  
}

@media (min-width: 900px) { 

    .slide {
        height: 187px;
    }

    .first-slide {
        height: 405px;
    }    
}    
    
    @media (min-width: 992px) {   

        .slider-main {
            padding-top: 60px;
            padding-left: 30px;
            padding-right: 30px;
        }

        .slider {
            padding-bottom: 0;
        }

        .slider-main__wrapper {
            overflow: visible;
          }
        
        .slide {
            height: 215px;
        }
    
        .first-slide {
            height: 460px;
        }

        .slider-main__title {
            font-size: 40px;
            text-align: left;
            margin-bottom: 60px;
        }

        .slider .slick-next {
            right: 15px;
            top: -115px;
            left: auto;
            bottom: auto;
            transform: none;
        }   
    }    

    @media (min-width: 1100px) {
        .slide {
            height: 243px;
        }
    
        .first-slide {
            height: 517px;
        }    
    }  
    
    @media (min-width: $desktop-width) {
        .slider-main {
            width: 1280px;
            margin: 0 auto;
            padding-top: 120px;
            padding-left: 25px;
            padding-right: 25px;
        }
    
        .slider-main__wrapper {
            padding: 0;
        }

        .slider-main__title {
            font-size: 44px;
            margin-bottom: 80px;
        }
    
        .slide {
            height: 215px;
        }
    
        .first-slide {
            /*      1/2    */
            width: calc(var(--slider-width) / 2);
            height: 460px;
        }
        
        .group {
            /*      1/4    */
            width: calc(var(--slider-width) / 4);
        }

        .slider .slick-next {
            top: -140px;
            padding-right: 20px;
            background-position: (88%, center);
        }
    }
    
    
    .video-controls {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);   
    }
    
    .play-pause {
        position: relative;
        top: 0;
        left: 0;
        width: 56px;
        height: 56px;
        background-color: #E53935;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("../img/icon-triangle.svg");
        border: none;    
        outline-offset: 5px;
        outline: 1px solid rgba(299, 57, 53, 0.20);
        border-radius: 0;   
        font-size: 0;
        z-index: 1000;   
        cursor: pointer;     
    }
    
    .play-pause:hover {

        &:hover {
            &::before {
            content: "";
            position: absolute;
            top: -5px;
            left: -5px;
            width: 66px;
            height: 66px;
            background-color: transparent;
            border: 5px solid rgba(299, 57, 53, 0.20); 
            }
        }

        &:active {
            &::before {
            content: "";
            position: absolute;
            top: -5px;
            left: -5px;
            width: 66px;
            height: 66px;
            background-color: transparent;
            border: 5px solid rgba(299, 57, 53, 0.20); 
            }
        }
        
    }
    
   
   

