*,::after,::before{box-sizing:border-box}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Roboto-regular", "Arial", sans-serif;
  color: #575757;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
}


a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  overflow: hidden;
  text-overflow: ellipsis;
}
