.scroll-to-top {
   display: none;
}

@media (min-width: $desktop-width) {
    .scroll-to-top {
        opacity: 0;  
        height: 0;
    }

    .scroll-to-top--show {
        display: block;
        opacity: 1;
        position: fixed;
        bottom: 300px;  
        right: 0;
        width: 68px;
        height: 68px;
        background-color: #E53935;        
        border: none;
        z-index: 10000;
        
        &:hover {
            background-color: #000000;
            box-shadow: 0px 20px 20px -8px rgba(34, 60, 80, 0.35);
        }

        
        &:active {
            background-color: #000000;
            box-shadow: 0px 20px 20px -8px rgba(34, 60, 80, 0.35);
        }
    }
}    

