.car {
    position: relative;
    cursor: pointer;       
    outline: 1px solid #E1E2E3;

    &:hover {
        box-shadow: 0px 30px 30px -15px rgba(34, 60, 80, 0.2);
    }

    &:active {
        box-shadow: 0px 30px 30px -15px rgba(34, 60, 80, 0.2);
    }

}

.car__img {
    height: 190px;
    object-fit: cover;
    margin-bottom: 24px;
}

.car__fig {
    padding: 0;
    margin: 0;
}

.car__figcaption {
    padding-left: 30px;
    padding-right: 30px;
    font-family: "Roboto-bold";
    color: #000000;
    font-size: 17px;
    margin-bottom: 15px;
}

.car__features {    
    padding-left: 30px;
    padding-right: 30px;
    color: #000000;
    font-size: 13px;
    margin-bottom: 20px;   

    span {        
        padding-right: 10px;
        z-index: 10;
        background-color: #ffffff;

        &:nth-child(2n) {
            padding-right: 0;
            padding-left: 10px;
        }
    }
}

.car__wrap-link {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: transparent;
}

.car__feature {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 8px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        width: 100%;
        height: 1px;
        background-color: #eeeeee;
    }

}

.car__price {    
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    font-family: "Roboto-bold";
    color: #E53935;
    font-size: 24px;
}

@media (min-width: 992px) {

    .car__img {
        width: 100%;
        height: 190px;
        margin-bottom: 26px;
    }

    .car__features {
        margin-bottom: 25px;
    }

}    

.wc-block-product-categories-list li:nth-last-child(2) span {
	color: red;
}

@media (min-width: $desktop-width) {
    .car__feature {
        margin-bottom: 6px;
    }
}    