@charset "UTF-8";
*, ::after, ::before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Roboto-regular", "Arial", sans-serif;
  color: #575757;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "Roboto-regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Inter-Roboto.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto-medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto-bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TTNeue-regular";
  src: url("../fonts/TTNeue-Regular.woff2") format("woff2"), url("../fonts/TTNeue-Roboto.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

@media (min-width: 576px) {
  .container {
    padding-left: 35px;
    padding-right: 35px;
  }
}
@media (min-width: 768px) {
  .container {
    padding-left: 39px;
    padding-right: 39px;
  }
}
@media (min-width: 992px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 25px;
    padding-right: 25px;
    margin: 0 auto;
  }
}
.header {
  border-bottom: 1px solid #E1E2E3;
  background-color: #ffffff;
}

@media (max-width: 1279px) {
  .header--fixed {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2000;
  }
}
.main-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.main-navigation__logo {
  padding-top: 2px;
}

.main-navigation__address {
  display: none;
}

.main-navigation__callback {
  display: none;
}

.main-navigation__callback-tel {
  display: none;
}

.main-navigation__toggle {
  display: none;
}

.main-navigation__socials {
  justify-content: center;
}

.main-navigation__socials .socials__item--email {
  display: none;
}

@media (min-width: 380px) {
  .main-navigation__logo {
    margin-right: 25px;
  }
}
@media (min-width: 576px) {
  .main-navigation__logo {
    margin-right: 0;
  }
  .main-navigation__image {
    width: 71px;
    height: 58px;
  }
  .main-navigation__callback-tel {
    margin-left: 20px;
  }
  .main-navigation__socials {
    flex-grow: 1;
    justify-content: center;
  }
  .main-navigation__socials .socials__item--email {
    display: block;
  }
  .main-navigation__callback-tel {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 48px;
    height: 48px;
    background-color: #e53935;
    border-radius: 50%;
    cursor: pointer;
    font-size: 0;
    transition: all 0.3s linear;
  }
  .main-navigation__callback-tel::before {
    content: "";
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-tel-white.svg");
  }
  .main-navigation__callback-tel:hover {
    background-color: #000000;
  }
  .main-navigation__callback-tel:active {
    background-color: #000000;
  }
  .main-navigation__toggle {
    margin-left: 20px;
  }
  .main-navigation__socials .socials__item--whatsapp {
    margin-right: 20px;
  }
  .main-navigation__socials .socials__item--tel {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-navigation__image {
    width: 232px;
    height: 58px;
  }
  .main-navigation__toggle {
    width: 58px;
    height: 58px;
  }
  .main-navigation__callback-tel {
    width: 58px;
    height: 58px;
    margin-left: 0;
  }
}
@media (min-width: 992px) {
  .main-navigation__callback-tel {
    margin-left: 20px;
  }
  .main-navigation__callback {
    display: block;
    text-align: right;
    padding-bottom: 7px;
    margin-left: 30px;
  }
  .main-navigation__tel {
    display: block;
    position: relative;
    margin-bottom: 7px;
    font-family: "Roboto-bold";
    font-size: 20px;
    color: #000000;
  }
  .main-navigation__tel:hover {
    color: #E53935;
  }
  .main-navigation__tel:active {
    color: #E53935;
  }
  .main-navigation__tel::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -28px;
    transform: translate(0, -50%);
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-tel.svg");
  }
  .main-navigation__work {
    display: inline-block;
    position: relative;
    padding: 0;
    margin: 0;
    font-size: 14px;
  }
  .main-navigation__work::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -35px;
    transform: translate(0, -50%);
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-clock.svg");
  }
}
@media (min-width: 1280px) {
  .main-navigation {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .main-navigation__image {
    width: 249px;
    height: 62px;
  }
  .main-navigation__address {
    position: relative;
    display: block;
    max-width: 215px;
  }
  .main-navigation__address::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -30px;
    transform: translate(0, -50%);
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
    background-image: url("../img/icon-map.svg");
  }
  .main-navigation__socials {
    flex-grow: 1;
    align-items: flex-start;
    padding-bottom: 5px;
  }
  .main-navigation__address {
    padding-left: 100px;
    max-width: 260px;
    font-size: 16px;
    padding-bottom: 5px;
  }
  .main-navigation__address::before {
    left: 65px;
  }
  .main-navigation__callback {
    margin-left: 0;
    padding-bottom: 0;
    padding-right: 10px;
    padding-left: 30px;
    padding-bottom: 5px;
  }
  .main-navigation__callback-tel {
    width: 172px;
    height: 62px;
    margin-bottom: 5px;
    border-radius: 0;
    font-size: 14px;
    color: #ffffff;
  }
  .main-navigation__callback-tel::before {
    width: 0;
    height: 0;
  }
  .main-navigation__socials .socials__item--whatsapp {
    margin-right: 30px;
  }
}
@media (max-width: 1279px) {
  .main-navigation__toggle {
    width: 48px;
    height: 48px;
    border: 1px solid #E1E2E3;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    transform: translateX(0);
    transition: transform 0.3s ease-in;
    color: #000000;
  }
  .main-navigation__toggle-inner {
    width: 18px;
    height: 17px;
  }
  /*.main-navigation__toggle:focus {
    outline: 0;
    color: #E53935;
  }*/
  .main-navigation__toggle-bar,
  .main-navigation__toggle-inner::after,
  .main-navigation__toggle-inner::before {
    content: "";
    display: block;
    width: 18px;
    height: 2px;
    margin-bottom: 4px;
    background-color: currentColor;
    transition: transform 0.3s ease;
  }
  .main-navigation__toggle-bar,
  .main-navigation__toggle-inner::after {
    margin-bottom: 0;
  }
  .main-navigation__toggle-bar {
    margin-bottom: 4px;
  }
  .main-navigation__bottom {
    padding-top: 0;
  }
  .main-navigation__toggle--active .main-navigation__toggle-bar {
    opacity: 0;
  }
  .main-navigation__toggle--active .main-navigation__toggle-inner::before {
    transform: translateY(6px) rotateZ(135deg);
    color: #E53935;
  }
  .main-navigation__toggle--active .main-navigation__toggle-inner::after {
    transform: translateY(-6px) rotateZ(-135deg);
    color: #E53935;
  }
}
@media (max-width: 1279px) and (min-width: 768px) {
  .main-navigation__toggle {
    width: 58px;
    height: 58px;
  }
}
.menu-add {
  display: none;
}

.menu__bottom {
  display: block;
}

@media (min-width: 1280px) {
  .menu__bottom {
    display: block;
  }
}
.socials {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.socials__item {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: pink;
  margin-right: 20px;
}
.socials__item:nth-child(3n) {
  margin-right: 0;
}

.socials__link {
  text-decoration: none;
  color: inherit;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s linear;
}
.socials__link::before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 150;
}

.socials__link--telegram {
  background-color: #0088CC;
}
.socials__link--telegram::before {
  width: 22px;
  height: 18px;
  background-image: url("../img/icon-telegram.svg");
}
.socials__link--telegram:hover {
  background-color: #007CBA;
}
.socials__link--telegram:active {
  background-color: #007CBA;
}

.socials__link--whatsapp {
  background-color: #24CC63;
}
.socials__link--whatsapp::before {
  width: 24px;
  height: 24px;
  background-image: url("../img/icon-whatsapp.svg");
}
.socials__link--whatsapp:hover {
  background-color: #20AC55;
}
.socials__link--whatsapp:active {
  background-color: #20AC55;
}

.socials__link--email {
  background-color: #E53935;
}
.socials__link--email::before {
  width: 20px;
  height: 16px;
  background-image: url("../img/icon-email.svg");
}
.socials__link--email:hover {
  background-color: #000000;
}
.socials__link--email:active {
  background-color: #000000;
}

.socials__link--tel {
  background-color: #E53935;
}
.socials__link--tel::before {
  width: 24px;
  height: 24px;
  background-image: url("../img/icon-tel-white.svg");
}
.socials__link--tel:hover {
  background-color: #000000;
}
.socials__link--tel:active {
  background-color: #000000;
}

@media (min-width: 992px) {
  .socials__item {
    margin-right: 30px;
  }
}
.button {
  position: relative;
  text-align: center;
  background-color: #E53935;
  border: none;
  padding-top: 22px;
  padding-bottom: 22px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Roboto-bold";
  font-size: 17px;
