}
.button:hover {
  background-color: #000000;
}
.button:active {
  background-color: #000000;
}

.main-screen__consult .container {
  background: url(../img/main-bg-consult.jpg) no-repeat 102% -76%;
}

@media (max-width: 1279px) {
  body.nav--opened {
    overflow-y: hidden;
  }
  .nav {
    opacity: 0;
    width: 0;
    height: 0;
    transition: all 0.5s ease-out;
  }
  body.nav--opened .nav {
    opacity: 1;
    display: flex;
    position: absolute;
    top: 120px;
    left: 0;
    height: calc(100vh - 71px);
    width: 100%;
    padding: 0;
    padding-top: 10px;
    padding-bottom: 120px;
    overflow: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: flex-start;
