    border-bottom: none;
  }
  .nav__link {
    display: block;
    padding-top: 19px;
    padding-bottom: 19px;
    padding-left: 20px;
    cursor: pointer;
    font-size: 16px;
    font-family: "Roboto-bold";
    color: #1B1C20;
  }
  .nav__link:hover {
    color: #E53935;
  }
  .nav__link:active {
    color: #E53935;
  }
  .nav__link--active {
    display: none;
  }
  .nav__block-title {
    padding-bottom: 20px;
    padding-left: 20px;
    font-size: 16px;
    font-family: "Roboto-bold";
    color: #e53935;
    border-bottom: 1px solid #E1E2E3;
  }
  .nav__accordeon-container {
    position: relative;
    padding-top: 17px;
  }
  .nav__btn {
    display: block;
    background-color: transparent;
    border: 0;
    outline: 0;
    position: absolute;
    width: 100%;
    height: 58px;
    top: 0;
    left: 0;
    border: none;
  }
  .nav__btn-icon {
    position: absolute;
    right: 20px;
    top: 25px;
    background-repeat: no-repeat;
    background-size: contain;
    width: 10px;
    height: 10px;
    background-color: #ffffff;
    background-image: url("../img/icon-arrow-red.svg");
    border: none;
    transition: 0.2s ease-in;
  }
  .nav__accordeon-container--closed .nav__btn-icon {
    top: 18px;
    transform: translateY(50%) rotate(180deg);
    transition: 0.2s ease-in;
  }
  .nav__submenu {
    opacity: 1;
    margin: 0;
    padding: 0;
    padding-top: 22px;
    padding-left: 20px;
    list-style: none;
    background-color: #ffffff;
    box-sizing: content-box;
    max-height: 415px;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-color: dark;
    scrollbar-width: thin;
    overscroll-behavior: contain;
    transition: all 0.5s ease-out;
  }
  .nav__accordeon-container--closed .nav__submenu {
    padding: 0;
    margin: 0;
    margin-left: 40px;
    list-style: none;
    max-height: 0;
    opacity: 0;
    will-change: max-height;
    transition: all 0.5s ease-out;
  }
  .nav__submenu-link {
    display: block;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 20px;
    cursor: pointer;
    font-size: 15px;
    color: #575757;
  }
  .nav__submenu-link:hover {
    color: #E53935;
  }
  .nav__submenu-link:active {
    color: #E53935;
  }
  html .nav__submenu {
    scrollbar-width: thin;
    scrollbar-color: #e53935 #ddd;
  }
