    width: 30px;
    overflow: hidden;
    margin-right: 20px;
    border-left: 4px #DDDDDD solid;
    border-radius: 0;
  }
  .nav__submenu::-webkit-scrollbar-thumb {
    background-color: transparent;
    border-left: 4px #E53935 solid;
    border-radius: 0;
  }
  .nav::-webkit-scrollbar {
    background-color: #DDDDDD;
