    opacity: 0;
    margin: 0;
    padding-top: 70px;
    padding-bottom: 30px;
    padding-left: 40px;
    padding-right: 30px;
    list-style: none;
    column-count: 4;
    position: absolute;
    top: 20px;
    left: 0;
    width: 0;
    overflow: hidden;
    max-height: 0;
    background-color: #ffffff;
    z-index: 10;
    transition: all 0.5s ease-out;
    box-shadow: 0px 20px 30px -12px rgba(34, 60, 80, 0.1);
  }
  .nav__submenu::before {
    content: "";
    position: absolute;
    height: 27px;
    top: 0;
    left: 0;
    right: 0;
    background-color: #f5f5f5;
  }
  .nav__submenu-item {
    margin-bottom: 8px;
