.slider {
  display: block;
  --slider-width: 100%; /*    */
  --slider-gap: 10px; /*    */
  --slider-gap-mobile-big: 20px;
  padding-bottom: 112px;
  margin-left: -10px;
  /*margin-right: calc(20px / -2);*/
}

.group {
  display: grid !important;
  grid-template-rows: 1fr 1fr;
  grid-gap: var(--slider-gap);
}

.slide-content {
  position: relative;
  display: flex;
  /*padding: 0 calc(var(--slider-gap) / 2);*/
  width: 100%;
  height: 100%;
}

.video {
  position: relative;
  padding: 0;
  margin: 0;
  margin-left: 10px;
  padding-right: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: transparent;
}

.slide {
  position: relative;
}

.slider .slick-prev {
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
.slider .slick-prev::before {
  content: "";
}

.slider .slick-next {
  position: absolute;
  top: auto;
  bottom: -8px;
  right: 25px;
  z-index: 100;
  width: calc(100vw - 40px);
  height: 59px;
  padding-right: 18px;
  border: 1px solid #E1E2E3;
  background-position: 73% center;
  background-repeat: no-repeat;
  background-image: url("../img/icon-arrow-small.svg");
  background-color: transparent;
  box-sizing: border-box;
  z-index: 1000;
}
.slider .slick-next::before {
  content: " ";
  font-family: "Roboto-regular";
  font-size: 15px;
  color: #1B1C20;
}

.slider .slick-next:hover, .slider .slick-next:focus {
  background-image: url("../img/icon-arrow-active-small.svg");
}
.slider .slick-next:hover::before, .slider .slick-next:focus::before {
  color: #E53935;
}

