    display: block;
    opacity: 1;
    position: fixed;
    bottom: 300px;
    right: 0;
    width: 68px;
    height: 68px;
    background-color: #E53935;
    border: none;
    z-index: 10000;
  }
  .scroll-to-top--show:hover {
    background-color: #000000;
