.form-section {
    padding-top: 25px;
}

.form-section__wrap {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #E1E2E3;   
}

.form-section__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 20px;
    text-align: center;
}

.form-section__text {
    display: none;
}

@media (min-width: 576px) {   
    
    .form-section {
        padding-top: 35px;
    }

    .form-section__title {
        font-size: 24px;
        margin-bottom: 30px;
    }   

    .form-section__wrap {
        padding-top: 30px;
        padding-left: 30px;
        padding-right: 30px;            
        padding-bottom: 10px;
    }

}    

@media (min-width: 768px) {   
    .form-section {
        padding-top: 45px;
    }

    .form-section__wrap {
        padding-top: 30px;
        padding-bottom: 0;
    }

    .form-section__title {
        font-size: 30px;
    }
}    

@media (min-width: 992px) {   
    .form-section {
        padding-top: 55px;
    }

    .form-section__wrap {
        padding-bottom: 20px;
    }
}    

@media (min-width: $desktop-width) {
    .form-section {
        padding-top: 120px;
    }

    .form-section__wrap {
        padding-top: 50px;
        padding-bottom: 45px;
        padding-left: 40px;
        padding-right: 40px;
    }

    .form-section__title {
        margin-bottom: 40px;
    }

}    

  