.areas {
    padding-top: 2px;
    padding-bottom: 46px;    
}

.areas__wrap {
    background-repeat: no-repeat;
    background-position: 100% 30%;
    background-image: url("../img/map-bg.svg");
}

.areas__title {
    padding: 0;
    margin: 0;
    margin-bottom: 35px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 24px;
}

.areas__list {
    margin: 0;
    padding: 0;
    list-style: none;
    column-count: 2;    
    column-gap: 22px;
}

.areas__name {
    font-size: 15px;
    max-width: 210px;
    margin-bottom: 9px;
    color: #000000;
}

.areas__map {
    display: none;
}

@media (min-width: 576px) {
    .areas {
        padding-top: 0;
        padding-bottom: 58px;
    }

    .areas__title {
        font-size: 30px;
        text-align: center;
        margin-bottom: 45px;
    }

    .areas__name {
        font-size: 16px;
        margin-bottom: 9.5px;
    }

    .areas__list {
        column-gap: 30px;
    }

}    


@media (min-width: 768px) {

    .areas {
        padding-top: 7px;
        padding-bottom: 66px;
    }

    .areas__title {
        font-size: 36px;
        margin-bottom: 70px;
    }

    .areas__list {
        column-count: 3;
    }

    .areas__name {
        &:last-child {
            display: none;
        }
    }
}

@media (min-width: 992px) {

    .areas {
        padding-top: 10px;
    }

    .areas__title {
        font-size: 40px;
        text-align: left;
        margin-bottom: 55px;
    }

    .areas__name {
        max-width: 255px;
    }
} 

@media (min-width: $desktop-width) {
    .areas {
        padding-top: 70px;
        padding-bottom: 115px;
    }

    .areas__wrap {
        display: grid;
        grid-template-columns: 1fr 600px;
    }

    .areas__title {
        font-size: 44px;
        margin-bottom: 87px;
    }

    .areas__list {
        column-count: 2;
        column-gap: 10px;
    }

    .areas__name {
        max-width: 260px;
    }

    .areas__map {
        display: block;
    }
} 

