.callback-form {
    padding: 30px;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    border: 1px solid #F1F1F1;
}

.callback-form__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 22px;
    text-align: center;
}

.callback-form__section {
    margin-bottom: 20px;
}

.callback-form__text {
    width: 100%;
    padding-left: 30px;
    height: 70px;
    line-height: 68px;
    font-size: 15px;
    border: 1px solid #E1E2E3;
    box-sizing: border-box;

    &:hover {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:focus {
        box-shadow: 0px 0px 10px 0px rgba(34, 60, 80, 0.1);
        outline: none;
    }

    &:active {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }

    &:disabled {
        color: #E53935;
        box-shadow: 0px 0px 10px 0px rgba(299, 57, 53, 0.5);
        outline: none;
    }
}

.callback-form__send.button {
    width: 100%;
    padding-top: 26px;
    padding-bottom: 26px;
    font-size: 18px;
}

@media (min-width: 576px) {     

    .callback-form__section {
        margin-bottom: 30px;
    }
    
    .callback-form__field {
        left: 15px;
        right: 15px;
    }

    .callback-form__title {
        display: block;
        margin: 0 auto;
        margin-bottom: 40px;
        font-size: 24px;
        max-width: 400px;
    }

    .callback-form__field {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
    
}  

@media (min-width: 768px) {
    .callback-form__title {
        font-size: 28px;
        max-width: none;
    }

    .callback-form {
        padding-bottom: 40px;
    }
}    

@media (min-width: 992px) {
    .callback-form {
        padding-top: 40px;
        padding-bottom: 50px;
    }

    .callback-form__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;
    }

    .callback-form__title {       
        font-size: 30px;
    }

    .callback-form__field {
        grid-column: 1/3;
        gap: 40px;
    }

    .callback-form__section {
        margin-bottom: 0;
    }
    
    .callback-form__send.button {
        width: auto;
        grid-column: 3/4;
    }
}    

@media (min-width: $desktop-width) {
    .callback-form {
        padding-top: 45px;
        padding-bottom: 65px;
        padding-left: 70px;
        padding-right: 70px;
    }
}    