@font-face {
    font-family: "Roboto-regular";
    src: url("../fonts/Roboto-Regular.woff2") format("woff2"),
      url("../fonts/Inter-Roboto.woff") format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
  font-family: "Roboto-medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"),
    url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
    font-family: "Roboto-bold";
    src: url("../fonts/Roboto-Bold.woff2") format("woff2"),
      url("../fonts/Roboto-Bold.woff") format("woff");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
  font-family: "TTNeue-regular";
  src: url("../fonts/TTNeue-Regular.woff2") format("woff2"),
    url("../fonts/TTNeue-Roboto.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}





