.main-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.main-navigation__logo {
    padding-top: 2px;
}

.main-navigation__address {
    display: none;
}

.main-navigation__callback {
    display: none;
}

.main-navigation__callback-tel {
    display: none;
}

.main-navigation__toggle {
    display: none;
}

.main-navigation__socials {      
    justify-content: center;
}

.main-navigation__socials .socials__item--email {
    display: none;
}

@media (min-width: 380px) {   
    .main-navigation__logo {
        margin-right: 25px;
    }
}    

@media (min-width: 576px) {  
    
    .main-navigation__logo {
        margin-right: 0;
    }

    .main-navigation__image {
        width: 71px;
        height: 58px;
    }

    .main-navigation__callback-tel {
        margin-left: 20px;
    }    
    

    .main-navigation__socials {  
        flex-grow: 1;
        justify-content: center;
    }
    
    .main-navigation__socials .socials__item--email {
        display: block;
    }

    .main-navigation__callback-tel {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 48px;
        height: 48px;
        background-color: #e53935;
        border-radius: 50%;
        cursor: pointer;
        font-size: 0;
        transition: all 0.3s linear;
    
        &::before {
            content: "";
            display: block;
            background-repeat: no-repeat;
            background-size: contain;            
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-tel-white.svg");
        }
    
        &:hover {
            background-color: #000000;
        }
    
        &:active {
            background-color: #000000;
        }
    }
    

    .main-navigation__toggle {
        margin-left: 20px;
    }

    .main-navigation__socials .socials__item--whatsapp {
        margin-right: 20px;
    }

    .main-navigation__socials .socials__item--tel {
        display: none;
    }
}

@media (min-width: 768px) {
    .main-navigation__image {
        width: 232px;
        height: 58px;
    }

    .main-navigation__toggle {
        width: 58px;
        height: 58px;
    }    

    .main-navigation__callback-tel {
        width: 58px;
        height: 58px;
        margin-left: 0;
    }    
}

@media (min-width: 992px) { 

    .main-navigation__callback-tel {
        margin-left: 20px;
    }
    
    .main-navigation__callback {
        display: block;
        text-align: right;
        padding-bottom: 7px;
        margin-left: 30px;
    }

    .main-navigation__tel {
        display: block;
        position: relative;
        margin-bottom: 7px;
        font-family: "Roboto-bold";
        font-size: 20px;
        color: #000000;

        &:hover {
            color: #E53935;
        }
    
        &:active {
            color: #E53935;
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -28px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-tel.svg");
        }
    }

    .main-navigation__work {
        display: inline-block;
        position: relative;
        padding: 0;
        margin: 0;
        font-size: 14px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -35px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-clock.svg");
        }
    }
}    

@media (min-width: $desktop-width) {
    .main-navigation {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .main-navigation__image {
        width: 249px;
        height: 62px;
    }


    .main-navigation__address {
        position: relative;
        display: block;
        max-width: 215px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -30px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-map.svg");
        }
    }

    .main-navigation__socials {
        flex-grow: 1;
        align-items: flex-start;
        padding-bottom: 5px;
    }   
    

    .main-navigation__address {
        padding-left: 100px;
        max-width: 260px;
        font-size: 16px;
        padding-bottom: 5px;

        &::before {
            left: 65px;
        }
    }

    .main-navigation__callback {
        margin-left: 0;
        padding-bottom: 0;
        padding-right: 10px;
        padding-left: 30px;
        padding-bottom: 5px;
    }

    .main-navigation__callback-tel {
        width: 172px;
        height: 62px;
        margin-bottom: 5px;
        border-radius: 0;
        font-size: 14px;
        color: #ffffff;

        &::before {
            width: 0;
            height: 0;
        }
    }

    .main-navigation__socials .socials__item--whatsapp {
        margin-right: 30px;
    }
}

@media (max-width: $mobile-width-only) {
    .main-navigation__toggle {
        width: 48px;
        height: 48px;
        border: 1px solid #E1E2E3;
        border-radius: 50%;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #ffffff;
        transform: translateX(0);
        transition: transform 0.3s ease-in;
        color: #000000;
      }

      .main-navigation__toggle-inner {
        width: 18px;
        height: 17px;
      }
    
      /*.main-navigation__toggle:focus {
        outline: 0;
        color: #E53935;
      }*/
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after,
      .main-navigation__toggle-inner::before {
        content: "";
        display: block;
        width: 18px;
        height: 2px;
        margin-bottom: 4px;
        background-color: currentColor;
        transition: transform 0.3s ease;
      }
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after {
        margin-bottom: 0;
      }
    
      .main-navigation__toggle-bar {
        margin-bottom: 4px;
      }
    
      .main-navigation__bottom {
        padding-top: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-bar {
        opacity: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::before {
        transform: translateY(6px) rotateZ(135deg);
        color: #E53935;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::after {
        transform: translateY(-6px) rotateZ(-135deg);
        color: #E53935;
      }
    
}    

@media (max-width: $mobile-width-only) and (min-width: 768px) {
    .main-navigation__toggle {
        width: 58px;
        height: 58px;
    }
}    
