.deal {
    margin-bottom: 43px;
}

.deal__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 24px;
    text-align: center;
}

.deal__item {
    display: flex;
    justify-content: flex-start;   
    align-items: center;
    padding-top: 15px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 18px;
    border: 1px solid #EBEBEB;

    picture {
        flex-shrink: 0;
    }
}

.deal__text {
    padding: 0;
    padding-bottom: 10px;
    padding-left: 20px;
    margin: 0;
    font-family: "Roboto-medium";
    color: #1B1C20;
}

@media (min-width: 576px) {
    .deal {
        margin-bottom: 50px;
    }

    .deal__title {
        font-size: 30px;
        margin-bottom: 40px;
    }

    .deal__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .deal__item {
        padding-top: 20px;
        padding-bottom: 8px;
        flex-direction: column;
        margin-bottom: 0;
    }    

    .deal__img {
        margin-bottom: 14px;
    }

    .deal__text {
        padding-left: 0;
        text-align: center;
    }
}    

@media (min-width: $tablet-width) {
    .deal {
        margin-bottom: 60px;
    }

    .deal__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .deal__item {
        flex-direction: row;
        padding-bottom: 0;
    }

    .deal__text {
        text-align: left;
        padding-left: 20px;
        padding-bottom: 22px;
    }
}    

@media (min-width: 992px) {
    .deal__title {
        font-size: 40px;
        text-align: left;
        margin-bottom: 48px;
    }

    .deal__item {
        padding-top: 30px;
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 10px;
    }

    .deal__img {
        width: 118px;
    }

    .deal__text {
        padding-left: 30px;
        font-size: 18px;
    }
}    

@media (min-width: $desktop-width) {
    .deal {
        margin-bottom: 123px;
    }

    .deal__title {
        font-size: 44px;
        margin-bottom: 80px;
    }

    .deal__wrap {
        grid-template-columns: repeat(4, 1fr);
    }

    .deal__item {
        flex-direction: column;
    }

    .deal__img {
        width: 169px;
        margin-bottom: 22px;
    }

    .deal__text {
        padding-left: 0;
        text-align: center;
    }
}    