.footer {
    margin-top: -212px;  
}

.footer__top {
    padding-top: 468px;
    padding-bottom: 5px;
    background-color: #222222;
    color: #ffffff;
}

.footer__nav {
    margin: 0;
    margin-bottom: 43px;
    padding: 0;
    list-style: none;
}

.footer__item {
    margin-bottom: 7px;
}

.footer__link {
    font-size: 14px;
    color: #ffffff;

    &:hover {
        color: #E53935;
    }

    &:active {
        color: #E53935;
    }
}

.footer__callback {
    display: block;
}

.footer__callback.button {
    font-size: 14px;
}

.footer__contacts-list {
    margin-bottom: 20px;
    text-align: center;
}

.footer__contacts-address {
    display: none;
}

.footer__time {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
}

.footer__contacts-phone span {
    position: relative;
    padding-left: 25px;
    font-size: 18px;

    &::before {
        content: "";
        position: absolute;
        top: -4px;
        left: -9px;
        background-repeat: no-repeat;
        background-size: contain;            
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-clock.svg");
    }
}

.footer__phone-link {
    display: inline-block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 10px;
    font-family: "Roboto-bold";
    font-size: 22px;
    color: #ffffff;
    transition: all 0.3s linear;

    &::before {
        content: "";
        position: absolute;
        top: 4px;
        left: 3px;
        background-repeat: no-repeat;
        background-size: contain;            
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-tel.svg");
    }

    &:hover {
        color: #E53935;
    }

    &:active {
        color: #E53935;
    }
}

.footer__bottom {
    position: relative;
    padding-top: 24px;
    padding-bottom: 24px;
}

.footer__copyright {
    max-width: 220px;
    margin: 0 auto;
}

.footer__copyright-text {
    padding: 0;
    margin: 0;
    margin-bottom: 5px;
    font-family: "Roboto-medium";
    font-size: 14px;
    color: #000000;
    text-align: center;
}

.footer__copyright-link {
    bottom: 20px;
    left: 20px;
    color: #000000;

    &:hover {
        color: #E53935;
    }

    &:active {
        color: #E53935;
    }
}

.footer__socials {
    padding-top: 15px;
    max-width: 300px;
    margin: 0 auto;
}

.footer__socials.socials .socials__item {
    margin-right: 30px;

    &:nth-child(3n) {
        margin-right: 0;
    }
}

@media (min-width: 576px) {
    .footer__top {
        padding-top: 410px;
        padding-bottom: 30px;
    }

    .footer__nav {
        column-count: 2; 
        column-gap: 40px;
        margin-bottom: 36px;
    }

    .footer__item {
        max-width: 240px;
        margin-bottom: 10px;
    }

    .footer__contacts-list {
        padding-left: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;       
    }

    .footer__contacts-phone {        
        text-align: right;
        margin-right: 30px;
    }

    .footer__callback {
        flex-grow: 1;
    }
        
    .footer__phone-link {
        font-size: 16px;
    }

    .footer__contacts-phone span {
        font-size: 14px;
    }   

    .footer__time {
        margin-bottom: 0;
    }

    .footer__bottom {
        display: flex;
        justify-content: space-between;
    }

    .footer__socials {
        margin: 0;
        width: 200px;
        max-width: 200px;
    }

    .footer__socials.socials {
        padding-top: 0;
        justify-content: space-between;       
    }    

    .footer__socials.socials .socials__item {
        margin-right: 20px;
    }

    .footer__copyright {
        max-width: auto;
        margin: 0;
    }

    .footer__copyright-link {
        margin-bottom: 0;
        bottom: 28px;
        left: 35px;
    }

    .footer__copyright-link {
        position: absolute;
    }

}   


@media (min-width: 768px) {
    .footer__top {
        padding-top: 435px;
    }

    .footer__nav {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        margin-bottom: 30px;
    }    

    .footer__item {
        margin-bottom: 12px;

        &:last-child {
            display: none;
        }
    }

    .footer__item--first {
        order: 1;
    }

    .footer__item--second {
        order: 2;
    }

    .footer__item--third {
        order: 3;
    }

    .footer__item--forth {
        order: 4;
    }

    .footer__item--fifth {
        order: 5;
    }

    .footer__item--sixth  {
        order: 6;
    }

    .footer__item--seventh {
        order: 7;
    }

    .footer__item--eighth {
        order: 8;
    }

    .footer__item--ninth {
        order: 9;
    }

    .footer__item--tenth {
        order: 10;
    }

    .footer__item--eleventh {
        order: 11;
        margin-top: -20px;
    }

    .footer__item--twelfth {
        order: 12;
        margin-top: -18px;
    }

    .footer__item--thirteenth {
        order: 13;
    }

    .footer__item--fourteenth {
        order: 14;
        margin-top: -20px;
    }

    .footer__bottom {
        padding-top: 20px;
        padding-bottom: 20px;
        align-items: center;
    }

    .footer__contacts-list {
        padding-left: 0;
    }

    .footer__contacts-address {
        position: relative;
        display: block;
        max-width: 210px;
        padding-left: 30px;
        font-size: 14px;
        text-align: left;

        &::before {
            content: "";
            position: absolute;
            top: 7px;
            left: 3px;
            background-repeat: no-repeat;
            background-size: contain;            
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-map.svg"); 
        }
    }

    .footer__contacts-phone {
        padding-left: 30px;
        margin-right: 0;
    }
    
    .footer__callback {
        flex-grow: 0;
        width: 210px;
    }

    .footer__copyright-link {
        position: static;
    }

    .footer__copyright-text {
        margin-bottom: 0;       
        font-size: 15px;
    }

    .footer__copyright-text--policy {
        order: 1;
    }

}   

@media (min-width: 992px) {
    .footer__top {
        padding-top: 389px;
        padding-bottom: 50px;
    }

    .footer__item {
        margin-bottom: 14px;
    }

    .footer__link {
        font-size: 16px;
    }

    .footer__nav {
        margin-bottom: 45px;
    }

    .footer__socials {
        position: absolute;
        right: 34px;
        top: -124px;
    }

    .footer__contacts-list {
        width: 74%;
    }

    .footer__contacts-address {
        font-size: 16px;
    }

    .footer__contacts-phone {
        padding-right: 20px;
    }

    .footer__phone-link {
        font-size: 20px;
    }

    .footer__bottom {
        padding-top: 28px;
        padding-bottom: 28px;
    }
}    

@media (min-width: $desktop-width) {
    .footer__wrap {
        display: grid;
        grid-template-columns: 1fr 210px;
    }

    .footer__top {
        padding-top: 402px;
        padding-bottom: 25px;
    }

    .footer__nav {
        padding-right: 100px;
    }

    .footer__contacts-list {
        display: block;
        width: 210px;
        flex-direction: column;
    }

    .footer__contacts-address {
        text-align: right;
        margin-bottom: 23px;
    }

    .footer__contacts-phone {
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 20px;
    }

    .footer__socials {
        position: static;
        order: 1;
    }

    .footer__copyright-text {
        font-size: 16px;
    }

    .footer__bottom {
        padding-top: 18px;
        padding-bottom: 20px;
    }

}    