.documents {
    padding-top: 20px;
    padding-bottom: 20px;
}

.documents__title {
    padding: 0;
    margin: 0;
    margin-bottom: 50px;
    font-family: "Roboto-bold";
    color: #1B1C20;
    font-size: 24px;
    text-align: center;
}

.documents__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.documents__item {
    position: relative;
    padding-top: 160px;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 20px;
    border: 1px solid #EBEBEB;

    &::before {
        content: "";
        position: absolute;
        top: 30px;
        left: 30px;        
        background-repeat: no-repeat;
        background-size: cover;       
    }
}

.documents__item--contract {
    &::before {
        width: 180px;
        height: 100px;
        background-image: url('../img/document-1.png');
    }
}

.documents__item--act {
    &::before {
        width: 142px;
        height: 100px;
        background-image: url('../img/document-2.png');
    }
}

.documents__item-name {
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    font-family: "Roboto-bold";
    font-size: 16px;
    color: #1B1C20;
}

.documents__item-text {
    padding: 0;
    margin: 0;
    margin-bottom: 28px;
    font-size: 15px;

    a {
        color: #E53935;
        text-decoration: underline;

        &:hover {
            color: #1B1C20;
        }

        &:hover {
            color: #1B1C20;
        }
    }
}

@media (min-width: 576px) {
    .documents {
        padding-top: 32px;
    }

    .documents__title {
        font-size: 30px;
        margin-bottom: 42px;
    }

    .documents__item {
        padding-top: 155px;
        margin-bottom: 24px;
    }

    .documents__item-text {
        margin-bottom: 30px;
    }
}    
  

@media (min-width: 768px) {
    .documents {
        padding-bottom: 25px;
    }    

    .documents__title {
        font-size: 36px;
        margin-bottom: 50px;
    }

    .documents__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .documents__item {
        padding-top: 160px;
    }

    .documents__item-name {
        margin-bottom: 13px;
        font-size: 18px;
    }

    .documents__item-text {
        max-width: 250px;
        font-size: 16px;
    }
}

@media (min-width: 992px) {
    .documents {
        padding-top: 60px;
        padding-bottom: 0;
        margin-bottom: 0;
    }

    .documents__title {
        margin-bottom: 60px;
        font-size: 40px;
        text-align: left;
    }

    .documents__item {
        padding-top: 210px;
    }

    .documents__item--contract {
        &::before {
            width: 242px;
            height: 150px;
            background-image: url('../img/document-1-big.png');
        }
    }
    
    .documents__item--act {
        &::before {
            width: 197px;
            height: 149px;
            background-image: url('../img/document-2-big.png');
        }
    }    

    .documents__item-name {
        font-size: 18px;
        margin-bottom: 12px;
    }

    .documents__item-text {
        max-width: none;
        font-size: 16px;
    }
} 

@media (min-width: $desktop-width) {   
    .documents {
        padding-top: 120px;
        margin-bottom: 0;
    }
    
    .documents__title {
        font-size: 44px;
        margin-bottom: 80px;
    }
}    